*,
*:before,
*:after {
        box-sizing: border-box;
}

* {
        margin : 0;
        padding: 0;
        font   : inherit;
}

img,
picture,
svg,
video {
        display  : block;
        max-width: 100%;
}

body {
        background-color: #d6d7da;
        font-family: 'Poppins', sans-serif;
        min-height : 100vh;
}